import React, { useState } from "react";
import {
  Box,
  TextField,
  Button,
  Typography,
  Grid,
  Paper,
  useMediaQuery,
  useTheme,
  Container,
} from "@mui/material";
import { motion } from "framer-motion";
import emailjs from "emailjs-com";
import BackgroundAnimation from "./BackgroundAnimation";
import logo from "../Images/developers_logo_white.png";

const LandingSection = () => {
  const theme = useTheme();
  const matchesMd = useMediaQuery(theme.breakpoints.up("md"));
  const matchesSm = useMediaQuery(theme.breakpoints.down("sm"));

  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    emailjs
      .send("YOUR_SERVICE_ID", "YOUR_TEMPLATE_ID", formData, "YOUR_USER_ID")
      .then(
        (result) => {
          console.log(result.text);
          alert("Message Sent Successfully");
        },
        (error) => {
          console.log(error.text);
          alert("Message Failed to Send");
        }
      );

    setFormData({ name: "", email: "", message: "" });
  };

  return (
    <Box
      component={motion.div}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
      sx={{
        height: "100vh",
        width: "100vw",
        overflowY: matchesSm ? "auto" : "hidden",
        overflowX: "hidden",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        flexDirection: "column",
        padding: { xs: 2, md: 4 },
        background: "linear-gradient(135deg, #FF4081 30%, #2196F3 90%)",
        position: "relative",
      }}
    >
      <BackgroundAnimation />
      <Container sx={{zIndex:1}}>
        <Typography
          component={motion.h1}
          initial={{ y: -50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 1, delay: 0.4 }}
          variant="h4"
          gutterBottom
          sx={{ color: "#fff", textAlign: "center", mb: 2 }}
        >
          Professional Website Development
        </Typography>
        <Typography
          component={motion.p}
          initial={{ y: -30, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 1, delay: 0.6 }}
          variant="h6"
          gutterBottom
          sx={{ color: "#fff", textAlign: "center", mb: 4 }}
        >
          Get your website built at an unbeatable price!
        </Typography>
        <Grid container spacing={2}>
          <Grid item xs={12} md={6}>
            <Paper
              component={motion.div}
              initial={{ x: matchesMd ? -100 : 0, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              transition={{ duration: 1, delay: 0.8 }}
              sx={{
                padding: { xs: 2, md: 4 },
                bgcolor: "#fff",
                borderRadius: 2,
              }}
            >
              <Typography variant="h5" gutterBottom>
                Contact Us
              </Typography>
              <form onSubmit={handleSubmit}>
                <TextField
                  label="Name"
                  name="name"
                  value={formData.name}
                  onChange={handleChange}
                  fullWidth
                  margin="normal"
                  variant="outlined"
                  sx={{ mb: 2 }}
                />
                <TextField
                  label="Email"
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  fullWidth
                  margin="normal"
                  variant="outlined"
                  sx={{ mb: 2 }}
                />
                <TextField
                  label="Message"
                  name="message"
                  value={formData.message}
                  onChange={handleChange}
                  fullWidth
                  margin="normal"
                  variant="outlined"
                  multiline
                  rows={4}
                  sx={{ mb: 2 }}
                />
                <Button
                  component={motion.button}
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.9 }}
                  variant="contained"
                  color="primary"
                  type="submit"
                  sx={{ mt: 2, width: "100%" }}
                >
                  Send
                </Button>
              </form>
            </Paper>
          </Grid>

          <Grid
            item
            xs={12}
            md={6}
            sx={{
              display: "flex",
              alignItems: "center",
            }}
          >
            {/* <Box
              component="img"
              src={logo}
              alt="Logo"
              sx={{ width: 200, height: "auto", mb: 4 }}
            /> */}
            <Box
              component={motion.div}
              initial={{ x: matchesMd ? 100 : 0, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              transition={{ duration: 1, delay: 1 }}
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <Typography
                variant="h6"
                gutterBottom
                sx={{ color: "#fff", textAlign: "center", mb: 2 }}
              >
                Why Choose Us?
              </Typography>
              <Box
                component={motion.div}
                whileHover={{ scale: 1.1 }}
                sx={{
                  padding: 3,
                  bgcolor: "#FF4081",
                  borderRadius: 2,
                  color: "#fff",
                  mb: 2,
                  textAlign: "center",
                  width: { xs: "100%", md: "auto" },
                }}
              >
                High Quality
              </Box>
              <Box
                component={motion.div}
                whileHover={{ scale: 1.1 }}
                sx={{
                  padding: 3,
                  bgcolor: "#2196F3",
                  borderRadius: 2,
                  color: "#fff",
                  mb: 2,
                  textAlign: "center",
                  width: { xs: "100%", md: "auto" },
                }}
              >
                Affordable Prices
              </Box>
              <Box
                component={motion.div}
                whileHover={{ scale: 1.1 }}
                sx={{
                  padding: 3,
                  bgcolor: "#FFEB3B",
                  borderRadius: 2,
                  color: "#000",
                  textAlign: "center",
                  width: { xs: "100%", md: "auto" },
                }}
              >
                Fast Delivery
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default LandingSection;
