import React, { useEffect, useRef } from 'react';
import { Box } from '@mui/material';

const BackgroundAnimation = () => {
  const canvasRef = useRef(null);
  const mousePosition = useRef({ x: window.innerWidth / 2, y: window.innerHeight / 2 });
  const circles = useRef(
    Array.from({ length: 5 }, () => ({
      x: window.innerWidth / 2,
      y: window.innerHeight / 2
    }))
  );

  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');
    let animationFrameId;

    const draw = () => {
      const width = canvas.width;
      const height = canvas.height;
      context.clearRect(0, 0, width, height);

      // Interpolate each circle's position towards the mouse or previous circle's position
      const easeFactor = 0.05;
      circles.current.forEach((circle, index) => {
        const targetX = index === 0 ? mousePosition.current.x : circles.current[index - 1].x;
        const targetY = index === 0 ? mousePosition.current.y : circles.current[index - 1].y;

        circle.x += (targetX - circle.x) * easeFactor;
        circle.y += (targetY - circle.y) * easeFactor;

        context.beginPath();
        context.arc(circle.x, circle.y, 20, 0, Math.PI * 2);
        context.fillStyle = `rgba(255, 255, 255, ${0.5 - index * 0.1})`;
        context.fill();
        context.closePath();
      });

      animationFrameId = requestAnimationFrame(draw);
    };

    const resizeCanvas = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    const handleMouseMove = (event) => {
      mousePosition.current = { x: event.clientX, y: event.clientY };
    };

    window.addEventListener('resize', resizeCanvas);
    window.addEventListener('mousemove', handleMouseMove);
    resizeCanvas();
    draw();

    return () => {
      window.removeEventListener('resize', resizeCanvas);
      window.removeEventListener('mousemove', handleMouseMove);
      cancelAnimationFrame(animationFrameId);
    };
  }, []);

  return (
    <Box
      component="canvas"
      ref={canvasRef}
      sx={{
        position: 'absolute',
        top: 0,
        left: 25,
        width: '100%',
        height: '100%',
        zIndex: 0,
      }}
    />
  );
};

export default BackgroundAnimation;
